import {
    mapGetters,
    mapMutations,
    mapActions
} from "vuex";
import {
    shuffle
} from "common/js/util";

export const playlistMixin = {
    computed: {
        ...mapGetters(['playList'])
    },
    mounted() {
        this.handlePlayList(this.playList)
    },
    // keep-alive
    activated() {
        this.handlePlayList(this.playList)
    },
    watch: {
        playList(newValue) {
            this.handlePlayList(newValue)
        }
    },
    methods: {
        handlePlayList() {
            throw new Error('component  handlePlayList()')
        }
    },
}

export const playerMixin = {
    computed: {
        modeIcon() {
            return this.mode === 0 ?
                "icon-sequence" :
                this.mode === 1 ?
                "icon-loop" :
                "icon-random";
        },
        ...mapGetters(["sequenceList", "currentSong", "mode", "playList", "favoriteList"])
    },
    methods: {
        //    
        changeMode() {
            const mode = (this.mode + 1) % 3;
            this.setMode(mode);
            let list = null;
            if (mode === 2) {
                list = shuffle(this.sequenceList);
            } else {
                list = this.sequenceList;
            }
            // 
            this._resetCurrentIndex(list);
            this.setPlayList(list);
        },
        // index 
        _resetCurrentIndex(list) {
            let index = list.findIndex(item => {
                return item.id === this.currentSong.id;
            });
            this.setIndex(index);
        },
        // 
        getFavoriteIcon(song) {
            if (this.ifFavorite(song)) return 'icon-favorite'
            return 'icon-not-favorite'
        },
        clickFavorite(song) {
            if (this.ifFavorite(song)) this.deleteFavoriteList(song)
            else this.saveFavoriteList(song)
        },
        ifFavorite(song) {
            const index = this.favoriteList.findIndex(item => {
                return item.id === song.id
            })
            return index > -1
        },
        ...mapMutations({
            setPlaying: "SET_PlAYING",
            setIndex: "SET_CURRENTINDEX",
            setMode: "SET_MODE",
            setPlayList: "SET_PLAYLIST"
        }),
        ...mapActions(['saveFavoriteList', 'deleteFavoriteList'])
    },
}

export const searchMixin = {
    data() {
        return {
            query: ''
        }
    },
    computed: {
        ...mapGetters(["searchHistory"])
    },
    methods: {
        // 
        saveSearch() {
            this.saveSearchHistory(this.query);
        },
        // query
        queryChange(query) {
            this.query = query;
        },
        // 
        addQuery(query) {
            this.$refs.searchBox.setQuery(query);
        },
        ...mapActions([
            "saveSearchHistory",
            "deleteSearchHistory"
        ])
    },
}